"use client";
import { useSelector } from "react-redux";
import JobCard from "@/components/JobCard";
import { useEffect, useState } from "react";
import axios from "axios";
import { COMPANY_API_ENDPOINT } from "@/lib/constant";

const SavedJobs = () => {
  const savedJobs = useSelector((state) => state.job.savedJobs);
  const [companyInfo, setCompanyInfo] = useState(null);
  console.log("saved jobs", savedJobs);

  const fetchCompanyDetails = async (companyId) => {
    try {
      const res = await axios.get(`${COMPANY_API_ENDPOINT}/get/${companyId}`);
      console.log("Comapany RES", res);
    } catch (error) {
      console.error("Error fetching company details:", error);
      return null;
    }
  };

  useEffect(() => {
    fetchCompanyDetails(savedJobs.company);
  }, []);

  return (
    <section className="wrapper py-8 px-4 sm:px-6 2xl:px-0">
      <h1 className="text-3xl font-bold mb-6 text-center md:text-left">
        Saved <span className="text-purple-600">Jobs</span>
      </h1>
      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {savedJobs?.map((job) => (
          <JobCard key={job._id} job={job} />
        ))}
      </div>
    </section>
  );
};

export default SavedJobs;
